@mixin font-face($font-family, $file-name, $font-weight) {
  @font-face {
    font-family: $font-family;
    font-weight: $font-weight;
    font-style: normal;
    src: url("../fonts/#{$file-name}.woff2") format("woff2");
    font-display: swap;
  }
}

@include font-face("notosans", "notosans", 400);
